$font-family: 'Roboto', sans-serif;;
html {
    scroll-behavior: smooth;
}

body{
    margin: 0;
    padding: 0;

    .body__bunner{
        width: 100%;
        height: 630px;
        position: absolute;
        z-index: -10;
        filter: brightness(1.9);
    }


}
@media (max-width: 1100px){
    body{
        .body__bunner{
            height: 470px;
        }        
    }
}

@media (max-width: 890px){
    body{
        .body__bunner{
            height: 350px;
        }        
    }
}

.main__contaner{
    width: 90%;
    max-width: 1590px;
    margin: 0px auto;
}
@media (max-width: 685px){
    .main__contaner{
        width: 100%;
        max-width: 1590px;
        margin: 0px auto;
        box-sizing: border-box;
    }
}


h1, h2, p {
    margin: 0px ;
    padding: 0px;
}

.section__header{
    padding: 50px 0px 35px 0px;
}

.header__our-contacts{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
}
@media (max-width: 890px){
    .header__our-contacts{
        display: flex;
        flex-wrap: wrap;
        align-items: flex-start;
        justify-content: space-between;
    }
}
@media (max-width: 685px){
    .header__our-contacts{
        padding: 0px 0px 0px 20px;
    }
}


.our-contacts__box-name{

    .box-name__title{
        color:#E37201;
        font-family: $font-family;
        font-weight: 700;
        font-size: 71px;
        line-height: 83px;
        margin: 0px 0px 34px 0px;
        text-shadow:
        2px  2px 0 rgb(255, 255, 255),
       -1px -1px 0 rgb(255, 255, 255),  
        1px -1px 0 rgb(255, 255, 255),
       -1px  1px 0 rgb(255, 255, 255),
        1px  1px 0 rgb(255, 255, 255);
    }

    .box-name__text{
        color: white;
        font-family: $font-family;
        font-weight: 700;
        font-size: 18px;
        line-height: 19px;

        &:not(:last-child){
            margin: 0px 0px 10px 0px;
        }
    }
}
@media (max-width: 1100px){
    .our-contacts__box-name{
        .box-name__title{
            font-size: 51px;
            margin: 0px 0px 20px 0px;
            line-height: 45px;
        }
    }
}
@media (max-width: 685px){
    .our-contacts__box-name{
        margin: 0px 0px 20px;
        .box-name__title{
            font-size: 45px;
        }
        
        .box-name__text{
            color: white;
            font-family: $font-family;
            font-weight: 500;
            font-size: 16px;
            line-height: 14px;
        }
    }
}


.our-contacts__box-adress{

    &>*:not(:last-child){
        margin: 0px 0px 22px 0px;
    }

    .box-adress__text{
    
        display: flex;
        align-items: center;
        justify-content: flex-start;
        text-decoration: none;
        color: white;

        img{
            width: 30px;
            height: 30px;
            display: block;
            margin: 0px 17px 0px 0px;
        }
        
        p{    
            font-family: $font-family;
            font-weight: 700;
            font-size: 24px;
            line-height: 28px;            
        }

    }
}
@media (max-width: 1100px){
    .our-contacts__box-adress{
        &>*:not(:last-child){
            margin: 0px 0px 15px 0px;
        }
        
        .box-adress__text{
            img{
                width: 20px;
                height: 20px;
            }
                
            p{    
                font-size: 18px;
            }
        
        }            
    }

}
@media (max-width: 890px){
    .our-contacts__box-adress{
        display: none;          
    }
}


.our-contacts__box-phones{

    &>*:not(:last-child){
        margin: 0px 0px 22px 0px;
        
    }
    .box-phones__text{
        display: flex;
        align-items: center;
        justify-content: flex-start;
        color: white;
        text-decoration: none;


        img{
            width: 30px;
            height: 30px;
            display: block;
            margin: 0px 17px 0px 0px;
        }
        
        p{
            font-family: $font-family;            
            font-weight: 700;
            font-size: 24px;
            line-height: 28px;            
        }

        &:last-child{
            display: none;
        }

    }
}
@media (max-width: 1100px){
    .our-contacts__box-phones{
        &>*:not(:last-child){
            margin: 0px 0px 15px 0px;
        }
        .box-phones__text{
            img{
                width: 20px;
                height: 20px;
            }
            
            p{    
                font-size: 18px;
            }
    
        }
    }
}
@media (max-width: 890px){
    .our-contacts__box-phones{
        .box-phones__text{
            &:last-child{
                display: flex;
            }
        }
    }
}
@media (max-width: 685px){
    .our-contacts__box-phones{
        .box-phones__text{
            &:last-child{
                display: flex;
            }

            p{
                font-weight: 500;
                font-size: 16px;
                line-height: 14px; 
            }
        }
    }
}


.section__main {
    width: 100%;
    // position: relative;
    filter: drop-shadow(0px 4px 24px rgba(0, 0, 0, 0.39));
}
.main-slider-bunner__box{
    width: 100%;
    position: relative;
    height: 580px;
}
@media (max-width: 1590px){
    .main-slider-bunner__box{
        height: 390px;
    } 
}
@media (max-width: 890px){
    .main-slider-bunner__box{
        height: 320px;
    } 
}
@media (max-width: 790px){
    .main-slider-bunner__box{
        height: 245px;
    }
}




.main-slider-bunner__contaner {
    width: 100%;
    position: absolute;
    background:#000000;

    .slider-bunner__image{
        display: block;
        width: 100%;
        height: 580px;
        object-fit: cover;
        animation: fade 14s linear infinite;
        animation-delay: 7s;
    }
    @keyframes fade {
        0% { opacity: 1 }
        10% { opacity: 1 }
        20% { opacity: 1 }
        30% { opacity: 1 }
        40% { opacity: 1 }
        50% { opacity: 0 }
        60% { opacity: 1 }
        70% { opacity: 1 }
        80% { opacity: 1 }
        90% { opacity: 1 }
        100% { opacity: 1 }
      }      
}
@media (max-width: 1590px){
    .main-slider-bunner__contaner {
    
        .slider-bunner__image{
            height: 390px;
        }   
    }
}
@media (max-width: 890px){
    .main-slider-bunner__contaner {
        .slider-bunner__image{
            height: 320px;
        }   
    }
}
@media (max-width: 790px){
    .main-slider-bunner__contaner {
        .slider-bunner__image{
            height: 245px;
        }   
    }
}


.slider-bunner__image-dark{
    position: absolute;
    width: 100%;
    height: 580px;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 10;        
}
@media (max-width: 1590px){
    .slider-bunner__image-dark{
        height: 390px;
    }
}
@media (max-width: 890px){
    .slider-bunner__image-dark{
        height: 320px;
    }
}
@media (max-width: 790px){
    .slider-bunner__image-dark{
        height: 245px;
    }
}


.section__center {
    width: 85%;
    margin: 0px auto;
}
@media (max-width: 1100px){
    .section__center {
        width: 90%;
    }
}


.offer {
    display: flex;
    justify-content: space-between;
    position: relative;
    z-index: 15;
    padding: 88px 0px 0px 0px;

    .offer__description {
        margin: 0px 10px 0px;
        .offer__description-title {
            margin:0px 0px 60px 0px;
            p{
                color: #FFFFFF;
                font-family: $font-family;
                font-weight: 700;
                font-size: 45px;
                line-height: 55px;
                span{
                    color:#ffffff;
                    letter-spacing: 3px;


                }
            }
        }
        .offer__description-text {
            p{
                color: #FFFFFF;
                font-family: $font-family;
                font-weight: 700;
                font-size: 24px;
                line-height: 35px;
            }
        }
        
    }
    .offer__form {
        width: 550px;
        border: 2px solid #F2F2F2;

        form{
            padding: 42px 18px;
            input{
                display: block;
                box-sizing: border-box;
                width: 100%;
                height: 50px;
                background: none;
                border: 2px solid #F2F2F2;
                color:#FFFFFF;
                font-family: $font-family;
                font-size: 16px;
                padding-left: 10px;
                outline: none;

                &:not(:last-child){
                    margin: 0px 0px 40px 0px;
                }

                &::placeholder{
                    color:#ffffff7a;
                    font-weight: 400;
                   
                }
            }

            .offer__form-button{
                cursor: pointer;
                width: 100%;
                height: 50px;
                font-family: $font-family;
                font-size: 16px;
                color:#FFFFFF;
                background:#329901;
                border: none;
                outline: none;
            }

            .status{
                color:#ffffff;
                font-size: 12px;
                font-family: $font-family;
            }

        }
    }
}
