.table-goods__box {
    background:#FFFFFF
}
.goods-filter__box {
    width: 100%;
    height: 80px;

    background:#000000;
    font-family: $font-family;
    font-weight: 700;
    font-size: 24px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin: 0px;
    padding: 0px;
}

.goods-filter {
    list-style-type: none;
    cursor: pointer;

    a{
        color: #FFFFFF;
        text-decoration: none;
        

        &:hover{
            color:#E37201;
            transition: all 0.4s ease;
        }        
    }


}

.table-goods__center-box{
    max-width: 1200px;
    margin: 0px auto;

    .table-goods__row {
        background:#ffffff;
        // height: 55px;
        font-family: $font-family;
        font-size: 18px;
        font-weight: 500;
        ul{
            margin:0px;
            padding: 0px;
            display: flex;
            align-items: center;
            justify-content: flex-start;
            height: 55px;

            li{
                list-style-type: none;
                width:200px;
                padding: 0px 0px 0px 20px;

                &:not(:last-child){
                    border-right: 1px solid #adadad;
                }

            }
            .table-goods__title{
                width: 100%;
                color:#329901;
            }

            .sale{
                color:#E37201;
            }
        }
        &:first-child{
            font-size: 22px;
            font-weight: 700;
        }
        &:nth-child(odd){
            background:#e4e4e4;
        } 
    }

}
@media (max-width: 590px){
    .goods-filter__box {
        font-size: 18px;

    }
    .table-goods__center-box{
        .table-goods__row {

            font-size: 14px;
            font-weight: 500;
            ul{
                height: 55px;
    
                li{
                    list-style-type: none;
                    width:200px;
                    padding: 0px 0px 0px 12px;
    
                }

            }
            &:first-child{
                font-size: 14px;
            }

        }
    
    }
}

@media (max-width: 400px){
    .goods-filter__box {
        font-size: 14px;

    }
    .table-goods__center-box{
        .table-goods__row {

            font-size: 12px;
            font-weight: 500;
            ul{
                height: 55px;
    
                li{
                    list-style-type: none;
                    width:200px;
                    padding: 0px 0px 0px 6px;
    
                }

            }
            &:first-child{
                font-size: 12px;
            }

        }
    
    }
}


footer{
    display: flex;
    align-items: center;
    width: 100%;
    height: 500px;
    background-size: cover;
    box-sizing:border-box;
    background:#000000;
    background-image:url('/img/wooden-background.jpg');

    .footer-flexbox__contaner {
        width: 100%;
        display: flex;
        justify-content: space-evenly;
        align-items: center;
    }

    .footer-contacts__box {

        .footer-contacts__title {
            color:#E37201;
            font-family: $font-family;
            font-weight: 700;
            font-size: 71px;
            line-height: 83px;
            margin: 0px 0px 34px 0px;
            text-shadow:
            2px  2px 0 rgb(255, 255, 255),
            -1px -1px 0 rgb(255, 255, 255),  
            1px -1px 0 rgb(255, 255, 255),
            -1px  1px 0 rgb(255, 255, 255),
            1px  1px 0 rgb(255, 255, 255);
        }

        a {
            color: #ffffff;
            display: flex;
            text-decoration: none;

            img{
                width: 30px;
                height: 30px;
                display: block;
                margin: 0 17px 0 0;
            }

            p{
                font-family: $font-family;
                font-weight: 700;
                font-size: 24px;
                line-height: 28px;
            }
        }

        a:not(:last-child){
            margin: 0px 0px 20px 0px;
        }

        .footer__button {
            cursor: pointer;
            display: flex;
            justify-content: center;
            align-items: center;
            width: 100%;
            height: 50px;
            font-size: 16px;
            font-family: $font-family;
            font-size: 16px;
            color: #fff;
            background: #329901;
            border: none;
            outline: 0;
        }   
    }

    .yandex-map__box {
        width: 50%;
    }
}

@media (max-width: 1050px){
